import React, { Component } from 'react';

import { 
    Link,
    withRouter,
     } from "react-router-dom"
import {getSinglePost, createComment, getPosts} from  "../../../action/blogAction"
import {connect} from 'react-redux'
import moment from "moment"


import Lottie from "react-lottie";
import * as CampaingLoading from '../../../loaders/loading.json'
import {Helmet} from "react-helmet"

const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: CampaingLoading.default,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };


class SinglePost extends Component {
    componentDidMount(){
        window.scrollTo(0, 0);
        if(this.props.match.params.slug ){
            this.props.getSinglePost(this.props.match.params.slug)
        }   
        this.props.getPosts()
    }
    handleSubmit = (e)=> {
        e.preventDefault()
        const {singlePost}= this.props.blog
        const {user, isAuthenticated}=this.props.auth
        const det={
            fullName:user.username,
            owner:user.id,
            post:singlePost._id,
            content:this.content.value
        }
        console.log(det)
        this.props.createComment(this.props.match.params.slug,det)
    }

    render() {
        const {singlePost, posts}= this.props.blog
        const {user, isAuthenticated}=this.props.auth
        return (
            <React.Fragment>
                   <Helmet>

                     
        <title>
            {singlePost ? singlePost.title : "elesarr.com is a crowdfunding website in nigeria"}
        </title>
        <meta 
            name="description" 
            content=  {`${singlePost ? singlePost.content : "elesarr.com is a crowdfunding website in nigeria"}`}
            />

<meta 
              name="keywords" 
              content={`
                      Nigeria's crowdfunding platform, 
                      Crowdfunding platform in Nigeria,
                      nigeria,
                      blockchain,
                      elesarr, 
                      crowdfunding in Nigeria, 
                      online platforms,
                      blockchain crowdfunding platforms,
                      choose elesarr,
                      elesarr crowdfunding platform,
                      ethereum in Nigeria,
                      Choose Elesarr today,
                      Get your project out there now on elesarr
                      creatives in nigeria funds,
                      small scale enterprenuers in nigeria funds
                      `} />

      </Helmet>


                {singlePost ? 
                    <React.Fragment>
                                          <div class="col-lg-8 posts-list">
               <div class="single-post">
                   {singlePost.image ? 
                        <div class="feature-img">

                           <img 
                              className="d-block w-100"
                              src={`${singlePost.image}`} 
                              alt=""
                              width="100px" 
                              height ="520px" 
                              // width="100%"
                              // height="100%"
                              
                              />
                        </div>
                
                   : 
                   null
                  //  <Lottie options ={defaultOptions} width ={"250px"} height ={"250px"}/>

                   }
                  <div class="blog_details">
                     <h2>{singlePost.title}
                     {/* <small>{singlePost.owner.username}</small> */}
                     </h2>
                     <ul class="blog-info-link mt-3 mb-4">
                        
                        <li><a href=""><i class="fas fa-clock"></i> 
                        {moment(singlePost.dateCreated).fromNow()}
                        </a></li>
                        <li><a href=""><i class="far fa-comments"></i> {singlePost.comments.length} Comments</a></li>
                     </ul>
                     <p 
                dangerouslySetInnerHTML={{__html:singlePost.content}}
                     >
                     </p>
                  </div>
               </div>
               {/* <div class="blog-author">
                  <div class="media align-items-center">
                     <img src="/new/img/blog/author.png" alt="" />
                     <div class="media-body">
                        <a href="#">
                           <h4>Harvard milan</h4>
                        </a>
                        <p

                        >Second divided from form fish beast made. Every of seas all gathered use saying you're, he
                           our dominion twon Second divided from</p>
                     </div>
                  </div>
               </div> */}
               <div class="comments-area">
            <h4>{singlePost.comments.length} Comments</h4>

                {singlePost.comments.map((comment, key)=> {
                    return(
                        <div class="comment-list">
                        <div class="single-comment justify-content-between d-flex">
                           <div class="user justify-content-between d-flex">
                              <div class="thumb">
                                 <img src={`https://image.flaticon.com/icons/svg/1738/1738691.svg`} alt="" />
                              </div>
                              <div class="desc">
                                 <p class="comment">
                                    {comment.content}
                                 </p>
                                 <div class="d-flex justify-content-between">
                                    <div class="d-flex align-items-center">
                                       <h5>
                                          <a>
                                          {comment.fullName}
                                              </a>
                                       </h5>
                    <p class="date">{moment(comment.dateCreated).fromNow()}</p>
                                    </div>
                                 </div>
                              </div>
                           </div>
                        </div>
                     </div>
   
                    )
                })}

               </div>
               {isAuthenticated === true ?
               
               <div class="comment-form">
                  <h4>Leave a Reply</h4>
                  <form 
                        class="form-contact comment_form" 
                        onSubmit={this.handleSubmit}
                        action="" id="commentForm">
                     <div class="row">
                        <div class="col-12">
                           <div class="form-group">
                              <textarea 
                                class="form-control w-100" 
                                name="comment" 
                                id="comment" 
                                cols="30" 
                                rows="9"
                                placeholder="Write Comment"
                                ref={(input) => this.content = input}
                                ></textarea>
                           </div>
                        </div>
                     </div>
                     <div class="form-group mt-3">
                        <button type="submit" class="btn_3 button-contactForm">Send Message</button>
                     </div>
                  </form>
               </div>
               
               :  null
               // <Lottie
               // options={defaultOptions}
               // height={"100%"}
               // width={"100%"}
            // /> 
               }
            </div>
            {posts ? 

            <div class="col-lg-4">
            <div class="blog_right_sidebar">
               <aside class="single_sidebar_widget popular_post_widget">
                  <h3 class="widget_title">Recent Post</h3>
                  {posts.map((post, key)=> {
                      return(
                         <div class="media post_item">
                         <img 

                             src={post.image}
                             width="70px" height ="70px"
                             alt="post"
                             />
                         <div class="media-body">
                         <Link
                                                     className="d-inline-block" 
                                                     to={{
                                                   pathname: `${`/blog/${post.slug}`}`,
                                                   state: {
                                                     id: `${post._id}`
                                                     
                                                   }
                                                 }}
                                                     >
                                                         {post.title}
                                                     </Link>
                            <p>created {moment(post.dateCreated).fromNow()}</p>
                         </div>
                      </div>
                      )
                  })}



               </aside>

               <aside class="single_sidebar_widget tag_cloud_widget">
                  <h4 class="widget_title">Tag Clouds</h4>
                  <ul class="list">
                     <li>
                        <Link >project</Link>
                     </li>
                     <li>
                        <Link>love</Link>
                     </li>
                     <li>
                        <Link >technology</Link>
                     </li>
                     <li>
                        <Link >travel</Link>
                     </li>
                     <li>
                        <Link >restaurant</Link>
                     </li>
                     <li>
                        <Link>life style</Link>
                     </li>
                     <li>
                        <Link>design</Link>
                     </li>
                     <li>
                        <Link >illustration</Link>
                     </li>
                  </ul>
               </aside>
               
            </div>
         </div>        
            : 
            null
            // <Lottie options ={defaultOptions} width ={"400px"} height ={"400px"}/>

            }
 
                    </React.Fragment>               
                : 
               //  null
                <Lottie options ={defaultOptions} width ={"600px"} height ={"600px"}/>
               }
            </React.Fragment>
        );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    blog:state.blog
  });
  // export default ProjectForm
  export default connect(mapStateToProps,{getSinglePost, createComment, getPosts})(
    withRouter(SinglePost)
  );